.popup-box {
	position: fixed;
	background: #00000050;
	width: 100%;
	height: 100vh;
	top: 0;
	left: 0;
}

.box {
	position: relative;
	width: 500px;
	margin: 0 auto;
	height: auto;
	max-height: 70vh;
	margin-top: calc(100vh - 85vh - 20px);
	background: #fff;
	border-radius: 20px;
	padding: 10px;
	padding-bottom: 20px;
	overflow: auto;
	filter: drop-shadow(0 0 1rem rgb(148, 148, 148));
}

.box-large {
	position: relative;
	width: 650px;
	margin: 0 auto;
	height: auto;
	max-height: 70vh;
	margin-top: calc(100vh - 85vh - 20px);
	background: #fff;
	border-radius: 20px;
	padding: 10px;
	padding-bottom: 20px;
	overflow: auto;
}

.box-large form {
	margin: 30px;
	font-family: Arial, Helvetica Neue, Helvetica, Sans-Serif;
	font-size: 16px;
}

.close-icon {
	content: 'x';
	cursor: pointer;
	position: absolute;
	right: 10px;
	top: 10px;
	background: #ffffff;
	width: 25px;
	height: 25px;
	border-radius: 50%;
	line-height: 20px;
	text-align: center;
	border: 2px solid rgb(170, 170, 170);
	color: rgb(170, 170, 170);
	font-size: 20px;
}

.close-icon:hover {
	background: #e1e1e1;
}

.popup-box h2,
h3 {
	text-align: center;
}

h2 {
	padding-top: 15px;
}
